import Vue from 'vue'
import VueRouter from 'vue-router'
import VueLazyLoad from 'vue-lazyload'

import Home from './components/Home.vue'
import app from './App.vue'

Vue.config.productionTip = false
Vue.use(VueRouter)
Vue.use(VueLazyLoad, {
  preLoad: 1.3,
  attempt: 1,
})

const router = new VueRouter({
  routes: [
    { path: '/', component: Home },
  ]
})

new Vue({
  router,
  render: h => h(app)
}).$mount('#app')
